<template>
  <div id="app">
    <router-view />
  </div>
</template>

<script>
export default {
  methods: {
    postPageHeight () {
      if (window.parent) {
        window.parent.postMessage(document.documentElement.scrollHeight, '*')
      }
    }
  },

  mounted () {
    this.postPageHeight()
    window.addEventListener('resize', this.postPageHeight)
  }
}
</script>
